.section {
  	height: 100vh; /* Fallback for browsers that do not support Custom Properties */
  	height: calc(var(--vh, 1vh) * 100);
  	width: 100%;
}

.container {
  	box-sizing: border-box;
  	width:100%;
    margin-left: auto;
    margin-right: auto;
  	padding-left: 15px;
    padding-right: 15px;
    text-align: center;
    
  	@include mq(small) {
  		max-width:540px;
  	}
  	@include mq(medium) {
  		max-width:720px;
  	}
  	@include mq(large) {
  		max-width:960px;
  	}
  	@include mq(xlarge) {
  		max-width:1140px;
  	}

}

.span--theme-red {
  color:$colorPrimary;
}